.App {
  font-family: sans-serif;
  text-align: center;
}

ul.mn {
  list-style: none;
  margin-left: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
ul.mn > li a {
  display: block;
  padding: 6px 10px;
  border: 1px solid transparent;
  transition: all 5ms ease;
}
ul.mn li a:hover {
  border-color: #ddd;
  background-color: #ddd;
  border-radius: 6px;
}
